import { normalizeProjectId } from './projectId'
import { axiosGeneration } from '@/lib/axios'

export async function getProjectScoped<T = any>(path: string, projectId: string, client = axiosGeneration): Promise<T> {
  const id = normalizeProjectId(projectId)
  const url = path.startsWith('projects/') ? path : `projects/${id}/${path}`
  const res = await client.get<T>(url)
  return res.data as T
}

export async function postProjectScoped<T = any>(path: string, projectId: string, data: any, client = axiosGeneration): Promise<T> {
  const id = normalizeProjectId(projectId)
  const url = path.startsWith('projects/') ? path : `projects/${id}/${path}`
  const res = await client.post<T>(url, data)
  return res.data as T
}

